@import "reset.scss";


@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');


body {
    font-family: 'Poppins'; }


.none {
    display: none !important; }

.container {
    padding: 0px 15px;
    max-width: 1170px;
    margin: 0 auto; }

.nav__toggle {
    display: none; }

.menu-icon-wrapper {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center; }


.menu-icon {
    position: relative;
    width: 30px;
    height: 4px;
    background-color: black; }


.menu-icon::before {
    position: absolute;
    left: 0;
    top: -10px;
    content: '';
    width: 30px;
    height: 4px;
    background-color: black;
    transition: transform 0.2s ease-in, top 0.2s linear 0.2s; }


.menu-icon::after {
    position: absolute;
    left: 0;
    top: 10px;
    content: '';
    width: 30px;
    height: 4px;
    background-color: black;
    transition: transform 0.2s ease-in, top 0.2s linear 0.2s; }



.menu-icon.menu-icon--active {
    background-color: transparent; }


.menu-icon.menu-icon--active::before {
    transform: rotate(45deg);
    top: 0;
    transition: top 0.2s linear, transform 0.2s ease-in 0.2s; }


.menu-icon.menu-icon--active::after {
    transform: rotate(-45deg);
    top: 0;
    transition: top 0.2s linear, transform 0.2s ease-in 0.2s; }


.header {
    overflow: hidden;
    padding-top: 46px;
    min-height: 770px;
    background: #F1F1F1;
    &__top {
        margin-bottom: 200px; }
    &__row {
        display: flex; }
    &__content {
        z-index: 1; }
    &__title {
        max-width: 482px;
        font-weight: 600;
        font-size: 44px;
        line-height: 66px;
        color: #000000;
        margin-bottom: 20px; }
    &__description {
        max-width: 500px;
        font-weight: 400;
        font-size: 14px;
        line-height: 200%;
        /* or 28px */


        color: #000000; }
    &__img {
        // overflow: hidden
        position: relative;
        flex-grow: 1;
        img {
            position: absolute;
            top: -123px;
            left: 176px;
            max-width: unset;
            width: 494px;
            height: 613px; } } }




.nav {
    display: flex;
    justify-content: space-between;
    height: 32px;
    &__list {
        display: flex;
        column-gap: 50px;
        align-items: flex-end;
        &-item-link {
            font-weight: 500;
            font-size: 14px;
            line-height: 21px;
            color: #000000;
            &.active {
                color: #606060; } } } }



.logo {
    position: relative;
    font-weight: 700;
    font-size: 32px;
    line-height: 100%;
    color: #000000;
    &::after {
        content: '';
        position: absolute;
        width: 6px;
        height: 6px;
        bottom: 0px;
        right: -8px;
        border-radius: 50%;
        background-color: #FDA300; } }


