.types {
    padding: 90px 0px;
    &__items {
        display: flex;
        column-gap: 30px; }
    &__item {
        width: calc(100%/3);
        height: 228px;
        &-logo {
            margin-bottom: 30px; }
        &-title {
            margin-bottom: 10px;
            font-weight: 600;
            font-size: 24px;
            line-height: 36px;
            text-transform: uppercase;
            color: #000000; }
        &-description {
            font-weight: 400;
            font-size: 14px;
            line-height: 200%;
            color: #000000; } } }


.portfolio {
    background: #F1F1F1;
    padding: 90px 0px;

    &__title {
        text-align: center;
        font-weight: 600;
        font-size: 34px;
        line-height: 51px;
        text-align: center;
        margin-bottom: 70px;
        color: #070707; }

    &__items {
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 90px; }

    &__img {
        margin-bottom: 40px; }
    &__text {
        text-align: center;
        text-decoration-line: underline; } }


.footer {
    padding: 90px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__content {
        max-width: 342px;
        text-align: center; }
    &__title {
        font-weight: 600;
        font-size: 34px;
        line-height: 51px;
        margin-bottom: 40px;
        color: #070707; }
    &__text {
        font-weight: 400;
        font-size: 18px;
        line-height: 27px;
        text-align: center;
        margin-bottom: 40px;

        /* Black */

        color: #070707; }
    &__btn {
        padding: 7.5px 23px;
        background: #070707;
        font-weight: 400;
        font-size: 18px;
        line-height: 27px;
        border-radius: 50px;
        color: #FFFFFF;
        margin-bottom: 80px; }
    &__icons {
        display: flex;
        column-gap: 61.35px;
        margin-bottom: 40px; }
    &__bottom-text {
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: #828282; } }





