@media  ( max-width: 1199px ) {
    .container {
         width: 960px; }
    .header {
        &__title {
            font-size: 38px;
            width: 450px; }
        &__description {
            font-size: 14px; }
        &__img {
            img {
                left: -50px; } } }
    .nav {
        &__list {
            &-item-link {
                font-size: 13px;
                column-gap: 40px; } }
        &__toggle {
            display: none; } } }


@media  ( max-width: 991px  ) {
    .header {
        min-height: 650px; }
    .container {
        width: 820px; }
    .header {
        padding-top: 40px;
        &__img {
            img {
                width: 400px;
                height: 500px;
                left: -80px; } } }
    .nav {
        &__list {
            display: none; }
        &__toggle {
            display: block; }
        &--mobile {
            padding: 46px 30px 0px 30px;
            position: fixed;
            left: 0px;
            top: 0px;
            background-color: #FFFFFF;
            width: 100%;
            height: 100%;
            z-index: 99;
            overflow-y: hidden;
            .nav__toggle {
                display: flex;
                align-items: flex-start; }
            .nav__list {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                row-gap: 30px; }
            .nav__list-item-link {
                font-size: 34px;
                line-height: 51px;
                color:#828282 {} }
            .active {
                color: black; }
            .nav__logo {
                display: none; } } }

    .menu-icon--active {
        &::before {
            background-color: #828282; }
        &::after {
            background-color: #828282; } } }




@media  ( max-width: 815px  ) {
    .container {
        width: 570px;
        padding: 0px 30px; }
    .header {
        &__top {
            margin-bottom: 90px; }
        &__row {
            flex-direction: column; }
        &__description {
            margin-bottom: 20px; }
        &__img {
            img {
                position: static; } } } }


@media  ( max-width: 560px  ) {
